@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 100%;
  /* filter: blur(8px);
  -webkit-filter: blur(8px); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    height:  50px;
    font-family: Poppins;
    display: flex;
    /* background-image: linear-gradient(to bottom right, black, white); */
    background: white;
    cursor: pointer;
}

.logo{
    font-weight: 800;
    margin-left: 15px;
    margin-top: 12px;
    width: 50%;
    color: black;
    font-weight: 700;
}

.socials{
    margin-top: 12px;
    margin-left: 30%;
    display: flex;
    justify-content: space-between;
    font-size: 30px;
    /* align-items: center; */
}

.social{
    margin-left: 20px;
    margin-right: 20px;
}

.lin{
    color:#0077b5 ;
    transition: all .5s;
}
.lin:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.git{
    color: #333;
    transition: all .5s;
}

.git:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
#search-bar{
    text-align: center;
    border-radius: 7px;
    border: none;
    height: 38px;
    width: 260px;
    background: rgba(0,0,0,0.9);
    color: white;
    font-weight: 700;
    cursor: pointer;
}

.input-feild{
    margin: 20px;
    text-align: center;
}
.search-result{
    background: rgba(0, 0, 0, 0.6);
    /* background: black; */
    /* background: white; */
    margin: 20px;
    color: white;
    width: 70%;
    display: flex;
    margin: auto;
    justify-content: space-around;
    align-items: center;
    border-radius: 20px;
    margin-bottom: 20px;
}

.left img {
    margin-top: 30px;
    margin-right: 40px;
    height: 200px;
    border-radius: 20px;
    margin-bottom: 30px;
    box-shadow:
    0 0 2px #fff,
    0 0 7px #fff,
    0 0 11px #fff,
    0 0 22px rgb(0, 183, 255),
    0 0 62px rgb(11, 165, 165),
    0 0 82px rgb(26, 76, 212),
    0 0 92px rgb(12, 36, 145),
    0 0 120px rgb(255, 30, 0);
    transition: all 0.5s;
}

.left img:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    box-shadow: none;
}

.test{
    font-weight: 500;
    font-size: 22px;
    color: white;
  text-shadow:
    0 0 2px #fff,
    0 0 7px #fff,
    0 0 11px #fff,
    0 0 22px rgb(0, 183, 255),
    0 0 62px rgb(11, 165, 165),
    0 0 82px rgb(26, 76, 212),
    0 0 92px rgb(12, 36, 145),
    0 0 120px rgb(255, 30, 0);
}

@media (max-width:800px )
{
    .left img{
        margin-left: 10px;
        height: 80px;
        margin-right: 15px;
        border-radius: 8px;
    }
    .search-result{
        font-size: 12px;
    }
    .test{
        font-size: 14px;
    }
}
.search-results{
    /* display: flex; */
    margin-bottom: 30px;
    border-radius: 10px;
    min-height: 700px;
    background: url(/fiction-characters/static/media/pop1.d35aa6ce.png);
}

